  height: 60px;
  font: 20px Scriptina;
  line-height: 1.4em;
  vertical-align: middle;
}
.art__quote span {
  display: inline-block;
  font-family: "Times New Roman" Times serif;
  font-size: 60px;
}
.left {
  height: 30px;
  line-height: 1em;
  margin-right: 50px;
  vertical-align: top;
}
.right {
  height: 30px;
  line-height: 1em;
  margin-left: 50px;
  vertical-align: bottom;
}
.tastes {
  display: inline-block;
  width: 620px;
  height: 580px;
  line-height: 580px;
  vertical-align: middle;
}
.tastes__sogood {
  display: inline-block;
  position: relative;
  line-height: normal;
  vertical-align: middle;
}
.tastes__sogood:after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 247px;
  height: 247px;
  background: url("../img/vector.png");
  opacity: 0.95;
}
.tastes__one {
  background: url("../img/img.jpg");
  margin: 0 20px 20px 0;
}
.tastes__two {
  background: url("../img/img0.jpg");
  margin-bottom: 20px;
}
.tastes__three {
  background: url("../img/img1.jpg");
  margin-right: 20px;
}
.tastes__four {
  background: url("../img/img2.jpg");
}
.tastes__photo {
  display: inline-block;
  position: relative;
  width: 294px;
  height: 252px;
  vertical-align: middle;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 50%;
}
.tastes__photo:after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 275px;
  height: 230px;
  border: 5px solid #fff;
}
.container3 {
  height: 760px;
  background: url("../img/placeholder.jpg");
  text-align: center;
  min-width: 1260px;
  width: 100%;
  background-size: cover;
}
.pancakes {
  display: inline-block;
  min-width: 1260px;
  max-width: 1600px;
  height: 760px;
  line-height: 760px;
}
.pancakes__wrapper {
  position: relative;
  display: inline-block;
  width: 1080px;
  height: 600px;
  line-height: normal;
  vertical-align: middle;
}
.pancakes__widget {
  position: absolute;
  bottom: 0;
  width: 970px;
  height: 520px;
  box-shadow: 3px -5px 43px -9px #bbb;
  background: url("../img/widget/4.jpg") no-repeat -430px -70px;
  background-size: 110%;
}
.pancakes__description {
  position: absolute;
  right: 0;
  width: 430px;
  height: 420px;
  padding: 60px 60px 40px;
  font-family: Alegreya;
  text-align: left;
  background: url("../img/widget/vektor.jpg") no-repeat 100% 100%;
  background-size: 200%;
}
.pancakes__description:before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 50px;
  height: 50px;
  transform: translate(-50%, -50%);
  border-radius: 50px;
  cursor: pointer;
  background: #fff url("../img/widget/menu.png") no-repeat 50% 50%;
}
.pancakes__name {
  font-size: 50px;
  letter-spacing: 2px;
  font-weight: 900;
}
.pancakes__subtitle {
  margin-bottom: 60px;
  font-size: 14px;
  letter-spacing: 0.2em;
  font-weight: 900;
  color: #a47d5e;
}
.pancakes__text {
  width: 398px;
  font: 10px Proxima;
  line-height: 2em;
  letter-spacing: 2px;
  font-weight: 600;
}
.pancakes__rating {
  display: inline-block;
  margin: 40px 0 55px -2px;
}
.pancakes__star {
  display: inline-block;
  width: 14px;
  height: 14px;
  font-size: 0;
  margin-right: 14px;
  cursor: pointer;
  background: url("../img/widget/starok.png");
}
.pancakes__star_none {
  background: url("../img/widget/starnone.png");
}
.pancakes__slider {
  position: relative;
  width: 430px;
  height: 90px;
  text-align: center;
}
.pancakes__slider:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 32px;
  height: 32px;
  transform: translateY(-50%);
  cursor: pointer;
  background: url("../img/widget/leftarrow.png");
}
.pancakes__slider:after {
  content: '';
  position: absolute;
  top: 50%;
  right: 0;
  width: 32px;
  height: 32px;
  transform: translateY(-50%);
  cursor: pointer;
  background: url("../img/widget/rightarrow.png");
}
.pancakes__icon {
  width: 90px;
  height: 90px;
  border-radius: 90px;
}
.pancakes__slider img:nth-child(2) {
  margin: 0 30px;
}
.pancakes__minutes {
  position: absolute;
  top: 0;
  right: 0;
  width: 200px;
  height: 200px;
  box-shadow: -12px 11px 73px 0px #bbb;
  font: 14px Alegreya;
  font-weight: 900;
  letter-spacing: 4px;
  color: #a47d5e;
  background: url("../img/widget/ready-in.jpg");
  z-index: 999;
}
.pancakes__ready {
  line-height: 75px;
}
.pancakes__digits {
  margin-top: -10px;
  font-size: 100px;
  line-height: 90px;
  color: #dfc691;
}
.pancakes__mins {
  color: #979285;
}
